


.profileClass {
    display: grid;
    grid-template-areas:
"avatar info"
;
    grid-template-columns: 1fr 2fr;
    margin: 0 auto;
    top: 0;
    height: auto;
}

.profileInfo {
    grid-area: info;
    margin-top: 0;
}

.avatarSide {
    grid-area: avatar;
    margin: 0 auto;
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr 1fr;
    height: 300px;
}

.avatarImg {
    height: 250px;
    width: 250px;
    border-radius: 5%;
    border: black solid 2px;
}

.fullPhoto {
    position: absolute;
    width: 500px;
    margin: 0 auto;
}

.inputPhoto {
    display: block;
    opacity: 0;
    height: 25px;
    width: 250px;
    z-index: 2;
    top: -25px;
    position: relative;
}

.labelUpdatePhoto {
    background-color: azure;
    color: #0e2121;
    text-align: center;
    width: 250px;
    height: 25px;
    border-radius: 5px;
    border: #0e2121 solid 1px;
}

.inputPhoto:hover {
    background-color: #530000;
}

.name {
    font-size: 30px;
    margin: 20px auto 20px auto;
    text-align: center;
}

.status {
    margin: 2%;
}

.aboutMe {
    margin: 2% 0 2% 0;
}

.editMode {
    margin: 10px;
}

.informationUser {
    background-color: azure;
    color: #0e2121;
    border-radius: 5px;
    border: #0e2121 solid 1px;
    padding: 3px 10px;
    margin: 2% 10% 2% 5px;
    width: 90%;
}

.buttonFollow {
    padding: 10px;
    grid-area: button;
    background-color: #530000;
    color: azure;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.6);
    text-decoration: none;
}

.buttonUnfollow {
    padding: 10px;
    grid-area: button;
    background-color: rgba(0, 0, 0, 0.41);
    color: azure;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.6);
}

.buttonEditInfo {
    grid-area: button;
    background-color: rgb(169, 169, 169);
    color: azure;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.6);
}

.buttonUnfollow:hover {
    background-color: rgb(84, 84, 84);
}

.profilePost {
    margin: auto;
}

.statusEditImg {
    margin-left: 10px;
    width: 20px;
}

